*,
*::before,
*::after {
  box-sizing: border-box;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.dragitem {
  display: flex;
  margin-bottom: 0.2em;
}

.dragitem > * {
  flex-grow: 1;
}

.dragitem__handle {
  width: 2em;
  flex-grow: 0;
  overflow: hidden;
}

.inputcopy {
  display: flex;
  height: 5em;
  /* border: 1px solid black; */
  place-inputcopys: center;
}

.inputcopy[data-is-over="true"] {
  background-color: red;
}

.inputcopy__input {
  height: 100%;
  border-radius: 0.3em;
  flex-grow: 1;
  resize: none;
  outline-color: hsl(180, 50%, 50%);
}

.inputcopy__button {
  width: 5em;
  height: 100%;
  border-radius: 0.3em;
}

.add:hover,
.inputcopy__button:hover {
  background-color: hsl(180, 50%, 70%);
}

.add:active,
.inputcopy__button:active {
  background-color: hsl(180, 50%, 50%);
}

.add {
  width: 5em;
  height: 3em;
  border-radius: 0.3em;
  background-color: hsl(180, 50%, 80%);
}

.inputcopy__button:active {
  background-color: hsl(180, 50%, 50%);
}

ul {
  padding: 0;
}

li {
  list-style-type: none;
}
